body {
   --clr-primary: hsl(209, 89%, 65%);
   --clr-primary-light: hsl(209, 90%, 80%);
   --clr-primary-dark: hsl(209, 90%, 25%);

   --clr-secondary: hsl(181, 51%, 48%);
   --clr-secondary-light: hsl(181, 51%, 80%);
   --clr-secondary-dark: hsl(181, 51%, 25%);


   --clr-tertiary: hsl(235, 32%, 50%);
   --clr-tertiary-light: hsl(235, 32%, 80%);
   --clr-tertiary-dark: hsl(235, 32%, 25%);
}